/* PARTICLE COMPONENT */

.particle_container {
  min-height: 100vh;
  background: midnightblue;
}

.particle_container_title {
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.particle_container_subtitle {
  color: darkgoldenrod;
  /* color: sandybrown; */
  /* color: burlywood; */
  /* color: darkkhaki; */
}

/* SUMMARY COMPONENT */

#summary_container {
  /* background: #e8e9ef; */
  background: #ffffff;
  padding-top: 20vh;
  padding-bottom: 20vh;
}

.summary_container_secondary_text_color {
  color: #44507f;
}

/* PORTFOLIO COMPONENT */

#portfolio_container {
  min-height: 80vh;
  padding-top: 20vh;
  padding-bottom: 20vh;
}

.company_card {
  min-height: 200px;
}

.company_card_image {
  height: 300px;
  width: 300px;
}

.animation_top {
  top: 150px;
  position: relative;
  /* background: red; */
}

.animation_bottom {
  bottom: 100px;
  position: relative;
}

/* 
Modal stuff below found for the Recipe Sections
*/

.modalx {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

#modal-main {
  position: fixed;
  /* background: #198bca; */
  color: #44507f;
  width: 55%;
  height: 65%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: auto;
}

hr {
  background: #fefefe;
}

.modal_image {
  width: 300px;
  height: 300px;
  /* top: 0%; */
  /* position: absolute; */
}

.modal_image_wrapper {
  width: 298px;
  height: 298px;
  overflow: hidden;
  /* text-align: center; */
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

